////
/// Menu
///
/// @group menus
////

/// Menu
///
/// @param {string}  $item    ['li']           Menu item selector
/// @param {string}  $submenu ['ul, .submenu'] Submenu selector
/// @param {boolean} $hover   [true]           Show submenu on mouse-over TODO
@mixin menu($item: 'li', $submenu: 'ul, .submenu', $hover: true) {
  list-style: none;
  padding-left: 0;
  padding-right: 0;

  // Remove menu on prints
  @media print {
    display: none;
  }

  #{$submenu} {
    list-style: none;
    padding-left: 0;
    padding-right: 0;
  }

  #{$item} {
    position: relative;
    cursor: default;

    a {
      // Make anchors as block-level element
      display: block;
    }

    // Hide submenu by default.
    > #{$submenu} {
      @include sr-only();
    }
  }

  // Show submenu on mouseover (if `.unbind-hover` is not set).
  // Show submenu if `.active` is set.
  //
  // This will reset the `sr-only` styles.
  @at-root {
    & #{$item}.active,
    &:not(.unbind-hover) #{$item}:hover {
      > #{$submenu} {
        position: static;
        min-width: 100%;
        width: auto;
        height: auto;
        margin: 0;
        overflow: visible;
        clip: auto;
      }
    }
  }
}

/// Menu dropdown
///
/// Works with ltr and rtl languages
///
/// @param {string} $submenu ['ul, .submenu'] Submenu selector
@mixin menu-dropdown($submenu: 'ul, .submenu') {
  #{$submenu} {
    position: absolute !important;
    z-index: $zindex-menu;

    @include ltr-only() {
      top: 100%;
      left: 0;
    }

    @include rtl-only() {
      left: auto;
      right: 0;
    }
  }
}

/// Vertical menu flyout
///
/// Works with ltr and rtl languages
///
/// @param {string} $submenu ['ul, .submenu'] Submenu selector
@mixin menu-flyout($submenu: 'ul, .submenu') {
  display: inline-block;

  // Most styles have to be `!important` to overwrite settings from groups
  #{$submenu} {
    position: absolute !important;
    z-index: $zindex-menu;
    top: 0 !important;

    @include ltr-only() {
      left: 100% !important;
    }

    @include rtl-only() {
      left: auto !important;
      right: 100% !important;
    }
  }
}
