//
// Groups
//

// Merge groups vertical by default.
.group {
  @include group();
}

.group-merge:not(.group-inline-xs):not(.group-inline-sm):not(.group-inline-md):not(.group-inline-lg):not(.group-inline-xl) {
  @include group-merge();
}

@each $breakpoint in map-keys($grid-breakpoints) {
  $breakpoint-prev: breakpoint-prev($breakpoint);

  @include media-breakpoint-up($breakpoint) {
    .group-inline-#{$breakpoint} {
      @include group-inline();

      &.group-merge {
        @include group-inline-merge();
      }
    }
  }

  @if $breakpoint-prev {
    @include media-breakpoint-down($breakpoint-prev) {
      .group-inline-#{$breakpoint}.group-merge {
        @include group-merge();
      }
    }
  }
}

//
// Horizontal alignments
//

.group-left {
  @include group-left();
}

.group-right {
  @include group-right();
}

.group-center {
  @include group-center();
}

//
// Vertical group alignments
//

.group-top {
  @include group-top();
}

.group-bottom {
  @include group-bottom();
}

.group-middle {
  @include group-middle();
}

.group-baseline {
  @include group-baseline();
}

//
// Vertical item alignments
//

.item-top {
  @include group-item-top();
}

.item-bottom {
  @include group-item-bottom();
}

.item-middle {
  @include group-item-middle();
}

.item-baseline {
  @include group-item-baseline();
}
