//
// Floatings
//

.float-left {
  @include float-left();
}

.float-right {
  @include float-right();
}

.float-none {
  float: none;
}

@each $bp in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($bp) {
    .float-left-#{$bp} {
      @include float-left();
    }

    .float-right-#{$bp} {
      @include float-right();
    }

    .float-none-#{$bp} {
      float: none;
    }
  }
}

//
// Clearfix
//

.clearfix {
  @include clearfix();
}

//
// Block level alignments
//

.align-center {
  @include align-center();
}

.align-left {
  @include align-left();
}

.align-right {
  @include align-right();
}
