/// Hide a file-upload input field but preserve it's functionality
/// 
/// This will wrap the input field over its parent element
///
/// @group forms
@mixin hidden-file-upload() {
  position: relative;
  overflow: hidden;

  [type="file"] {
    position: absolute;
    opacity: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
  }
}
