////
/// Groups
///
/// @group groups
////

/// Group items
///
/// @param {number} $gutter-width [$group-gutter-width]  Gutter between group items
/// @param {string} $items        [$group-item-selector] Group items
@mixin group($gutter-width: $group-gutter-width, $items: $group-item-selector) {
  > #{$items} {
    display: block;

    // &:not(:last-child) {
    //   margin-bottom: $gutter-width;
    // }
  }
}

/// Group items inline
///
/// @param {number} $gutter-width [$group-gutter-width]  Gutter between group items
/// @param {string} $items        [$group-item-selector] Group items
@mixin group-inline($gutter-width: $group-gutter-width, $items: $group-item-selector) {
  @include group-baseline($items);

  @if $enable-flex {
    display: flex
  } @else {
    // Collapse white-space between units
    // `!important` since it can be used as nested group
    letter-spacing: -0.28em !important; // YUI grid system is using -0.31em
    white-space: nowrap;
    display: block;

    > #{$items} {
      display: inline-block;
      letter-spacing: normal;
      white-space: normal;
    }
  }

  > #{$items} {
    width: auto;

    &:not(:last-child) {
      @include ltr-only() {
        margin-right: $gutter-width;
      }
      @include rtl-only() {
        margin-left: $gutter-width;
      }
    }
  }
}
