//
// Menus
//

.menu {
  @include menu();

  // Menus should work great with inline groups
  @each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
      &.group-inline-#{$breakpoint} {
        @include menu-dropdown();
      }
    }
  }

  // // This complexity to prevent `!important`
  // &.menu-flyout,
  // .menu-flyout {
  //   @include menu-flyout();
  // }
}

// ul.menu-flyout {
//   display: inline-block;
// }

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    .menu-flyout-#{$breakpoint} {
      @include menu-flyout();
    }
  }
}
