////
/// Merge group items
///
/// @group groups
////

/// Merge group items
///
/// Reset inner border radius and collapse border
///
/// @param {number} $gutter-width          [$group-gutter-width]          Gutter between group items
/// @param {number} $collapse-border-width [$group-collapse-border-width] Border width of group items
/// @param {string} $items                 [$group-item-selector]         Group items
@mixin group-merge($gutter-width: $group-gutter-width, $collapse-border-width: $group-collapse-border-width, $items: $group-item-selector) {
  margin-bottom: $gutter-width;

  > #{$items} {
    position: relative;

    // Reset inner border radius
    &:not(:first-child):not(:last-child) {
      border-radius: 0 !important;
    }

    &:first-child:not(:last-child) {
      @include border-bottom-radius(0 !important);
    }

    &:last-child:not(:first-child) {
      @include border-top-radius(0 !important);
    }

    // Collapse border
    &:not(:last-child) {
      margin-bottom: - $collapse-border-width !important;
    }

    // Fix border overlapping via z-index
    &:hover {
      z-index: $zindex-group-hover;
    }

    &:active,
    &:focus {
      z-index: $zindex-group-highlight;
    }
  }
}

/// Merge group items inline
///
/// Reset inner border radius and collapse border
///
/// @param {number} $gutter-width          [$group-gutter-width]          Gutter between group items
/// @param {number} $collapse-border-width [$group-collapse-border-width] Border width of group items
/// @param {string} $items                 [$group-item-selector]         Group items
@mixin group-inline-merge($gutter-width: $group-gutter-width, $collapse-border-width: $group-collapse-border-width, $items: $group-item-selector) {
  margin-right: $gutter-width;

  > #{$items} {
    position: relative;

    // Reset inner border radius
    &:not(:first-child):not(:last-child) {
      border-radius: 0 !important;
    }

    @include ltr-only() {
      &:first-child:not(:last-child) {
        @include border-right-radius(0 !important);
      }

      &:last-child:not(:first-child) {
        @include border-left-radius(0 !important);
      }

      // Collapse border
      &:not(:last-child) {
        margin-right: - $collapse-border-width !important;
      }
    }

    @include rtl-only() {
      &:first-child {
        @include border-left-radius(0 !important);
      }

      &:last-child {
        @include border-right-radius(0 !important);
      }

      // Collapse border
      &:not(:last-child) {
        margin-left: - $collapse-border-width !important;
      }
    }

    // Fix border overlapping via z-index
    &:hover {
      z-index: $zindex-group-hover;
    }

    &:active,
    &:focus {
      z-index: $zindex-group-highlight;
    }
  }
}
