////
/// Block level alignmentss
///
/// @group helpers
/// @see $enable-auto-direction
////

/// Center aligned block level element
@mixin align-center() {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/// Left aligned block level element
///
/// @param {boolean} $auto-direction
@mixin align-left($auto-direction: $enable-auto-direction) {
  @if $auto-direction and $direction == 'rtl' {
    @include align-right(false);
  } @else {
    display: block;
    margin-left: 0;
    margin-right: auto;
  }
}

/// Right eligned block level element
@mixin align-right($auto-direction: $enable-auto-direction) {
  @if $auto-direction and $direction == 'rtl' {
    @include align-left(false);
  } @else {
    display: block;
    margin-left: auto;
    margin-right: 0;
  }
}
