////
/// Floatings
///
/// @group helpers
/// @see $enable-auto-direction
////

/// Float left
///
/// @param {boolean} $auto-direction
@mixin float-left($auto-direction: $enable-auto-direction) {
  @if $auto-direction and $direction == 'rtl' {
    @include float-right(false);
  } @else {
    float: left;
    margin-left: 0;
  }
}

/// Float right
///
/// @param {boolean} $auto-direction
@mixin float-right($auto-direction: $enable-auto-direction) {
  @if $auto-direction and $direction == 'rtl' {
    @include float-left(false);
  } @else {
    float: right;
    margin-right: 0;
  }
}
