////
/// Group alignments
///
/// @group groups
////

//
// Horizontal alignments
//

/// Left aligned group
///
/// @param {boolean} $auto-direction [$enable-auto-direction]
///
/// @see $enable-auto-direction
@mixin group-left($auto-direction: $enable-auto-direction) {
  @if $auto-direction and $direction == 'rtl' {
    @include group-right(false);
  } @else {
    @if $enable-flex {
      justify-content: flex-start;
    } @else {
      text-align: left;
    }
  }
}

/// Right aligned group
///
/// @param {boolean} $auto-direction [$enable-auto-direction]
///
/// @see $enable-auto-direction
@mixin group-right($auto-direction: $enable-auto-direction) {
  @if $auto-direction and $direction == 'rtl' {
    @include group-left(false);
  } @else {
    @if $enable-flex {
      justify-content: flex-end;
    } @else {
      text-align: right;
    }
  }
}

/// Center aligned group
@mixin group-center() {
  @if $enable-flex {
    justify-content: center;
  } @else {
    text-align: center;
  }
}


//
// Vertical alignments
//

/// Top aligned group
///
/// @param {string} $items [$group-item-selector] Unsed if `$enable-flex` is true
@mixin group-top($items: $group-item-selector) {
  @if $enable-flex {
    align-items: flex-start;
  } @else {
    > #{$items} {
      @include group-item-top();
    }
  }
}

/// Bottom aligned group
///
/// @param {string} $items [$group-item-selector] Unused if `$enable-flex` is true
@mixin group-bottom($items: $group-item-selector) {
  @if $enable-flex {
    align-items: flex-end;
  } @else {
    > #{$items} {
      @include group-item-bottom();
    }
  }
}

/// Vertical middle aligned group
///
/// @param {string} $items [$group-item-selector] Unused if `$enable-flex` is true
@mixin group-middle($items: $group-item-selector) {
  @if $enable-flex {
    align-items: center;
  } @else {
    > #{$items} {
      @include group-item-middle();
    }
  }
}

/// Baseline aligned group
///
/// @param {string} $items [$group-item-selector] Unused if `$enable-flex` is true
@mixin group-baseline($items: $group-item-selector) {
  @if $enable-flex {
    align-items: baseline;
  } @else {
    > #{$items} {
      @include group-item-baseline();
    }
  }
}


//
// Vertical item alignments
//

/// Top aligned group-item
@mixin group-item-top() {
  @if $enable-flex {
    align-self: flex-start;
  } @else {
    vertical-align: top;
  }
}

/// Bottom aligned group-item
@mixin group-item-bottom() {
  @if $enable-flex {
    align-self: flex-end;
  } @else {
    vertical-align: bottom;
  }
}

/// Vertical middle aligned group-item
@mixin group-item-middle() {
  @if $enable-flex {
    align-self: center;
  } @else {
    vertical-align: middle;
  }
}

/// Baseline aligned group-item
@mixin group-item-baseline() {
  @if $enable-flex {
    align-self: baseline;
  } @else {
    vertical-align: baseline;
  }
}
