// Force a canvas to cover the full page (width/height) on the current view.

.full-page-width {
  @include full-page-width();
}

.full-page-height {
  @include full-page-height();
}

.full-page {
  @include full-page();
}

// Overlay
.overlay {
  @include overlay();
}

// Sticky top/bottom and 100% width.

.sticky-top {
  @include sticky-top();
}

.sticky-bottom {
  @include sticky-bottom();
}

// Add support for equal height columns on Bootstraps grid system.
// Add this class to the `.row`.

.row-eq-height {
  @include row-eq-height();
}
