//
// Resets
//

html,
body {
  // Set to full height of the browser window.
  min-height: 100%;
  // (Re-)set everything to border-box, 'cause border-box rockz!
  box-sizing: border-box;
  // Remove gray highlight when tapping links in Webkit browsers.
  // @see https://css-tricks.com/snippets/css/remove-gray-highlight-when-tapping-links-in-mobile-safari/
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  // Init reset to set children font-size relative to this one.
  font-size: 100%;
}

*,
*::before,
*::after {
  // Let the magic begin.
  box-sizing: inherit;
}

*:focus {
  // Remove outline in Chrome
  outline-width: 0;
}

// Typography
// --------------------------------------------------

#{$headers},
blockquote,
dd,
ul,
ol,
p,
hr,
pre {
  // Remove default margins
  margin: 0;
}

hr {
  // box-sizing for `hr` is set to `content-box` in normalize.css
  box-sizing: inherit;
}

// Remove space on sublists
ul,
ol {
  ul,
  ol {
    margin-bottom: 0;
  }
}

// Tables
// --------------------------------------------------

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;

  caption {
    // Center captions by default.
    text-align: center;
    // Table caption should be under the tables
    caption-side: bottom;
  }

  th,
  td {
    padding: 0;
    vertical-align: top;

    @include rtl-only() {
      text-align: right;
    }
    @include ltr-only() {
      text-align: left;
    }
  }
}

// Forms
// --------------------------------------------------

#{$input-fields} {
  color: inherit;
  // Enough space for touch-devices
  padding: .5rem .75rem;
  // Set minimal style to start in each browser at the same point
  border: 1px solid #bbb;

  // // Set minimal border on input fields
  // border: 1px solid $form-border-color;
  // &:hover {
  //   border-color: $form-border-color-hover;
  // }
  //
  // &:focus {
  //   border-color: $form-border-color-highlight;
  // }
}

select {
  // Remove 1px padding on top and bottom to catch the same height
  // as input fields and buttons
  padding-top: calc(.5rem - 1px);
  padding-bottom: calc(.5rem - 1px);
}

fieldset {
  // Fix Chrome's and FF's `min-width: min-content;`.
  min-width: 0;
  // Remove default outline behavior.
  padding: 0;
  margin: 0;
  border: 0;
}

::-moz-placeholder {
  // Fix FF opacity settings.
  opacity: 1;
  // Expected default color.
  color: #757575;
}

select[multiple],
select[size] {
  // Make multiple select elements height not fixed.
  height: auto;
}

[type="range"] {
  // Fix vertical align
  vertical-align: middle;
}

#{$buttons} {
  // Enough space for touch-devices
  // Top and bottom padding correspond to input padding
  padding: .5rem;
  // Don't allow text selection on buttons
  user-select: none;

  // Remove button appearance in WebKit
  -webkit-appearance: none;
  // Remove anchor styles
  text-decoration: none;
  color: inherit;
  // Make all button classes behave like buttons
  cursor: pointer;
  display: inline-block;

  // Set minimal style to start in each browser at the same point
  border: 1px solid #bbb;
  @include gradient-vertical(#fff, #ddd);

  &.active,
  &:active {
    @include gradient-vertical(#ddd, #fff);
  }

  // // Add some default colors to start styling in each browser at the same point.
  // // Setting a border will reset many space issues.
  // border: 1px solid $form-border-color;
  // background-color: $button-background;
  // border-width: 1px;
  //
  // &:hover {
  //   border-color: $form-border-color-hover;
  //   background-color: $button-background-hover;
  // }
  //
  // &:active {
  //   border-color: $form-border-color-highlight;
  //   background-color: $button-background-highlight;
  // }
}

// Images
// --------------------------------------------------

img {
  // Images should not overflow its parent.
  max-width: 100%;
  max-height: 100%;
  // Proportional scaling - now, we only have to set the width.
  height: auto;
  // Remove gap after images inside of anchors.
  // Makes it easier to handle icons in-line of text.
  vertical-align: top;
}

figure {
  // Remove default margin.
  margin: 0;
  // Do not wrap to 100% width, if not necessary.
  display: inline-block;
  max-width: 100%;

  img {
    display: block;
    margin: 0;
    padding: 0;
  }
}
