////
/// Full page
///
/// @group layout
////

/// Wrap element to full window width
@mixin full-page-width() {
  @include viewport-unit(width, 100vw);
}

/// Wrap element to full window height
@mixin full-page-height() {
  @include viewport-unit(height, 100vh);
}

/// Wrap element to full window width and height
@mixin full-page() {
  @include full-page-width();
  @include full-page-height();
}
